import Vue from 'vue'
import VueRouter from 'vue-router'
import MainLayout from '@/layouts/Main.vue'
import HomeView from '@/views/Home.vue'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/Home'
    },
    {
      path: '/',
      component: MainLayout,
      children: [
        {
          path: 'Home',
          name: 'home',
          components: { default: HomeView }
        }
      ]
    }
  ]
})
