<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex class="pa-2">
        <v-card>
          <v-card-title>
            {{ $t('pages.password') }}
          </v-card-title>

          <v-divider class="mx-4 my-0" />

          <v-card-text>
            <v-layout row wrap justify-space-around>
              <v-flex xs12 class="px-2">
                <h3>Entrada de letras</h3>
              </v-flex>

              <v-flex v-for="i in 5" :key="`container-${i}`" class="px-2" md2>
                <v-combobox
                  v-model="words[i - 1]"
                  multiple
                  chips
                  :label="$t(`password.word${i}`)"
                />
              </v-flex>

              <v-flex xs12>
                <v-divider class="mx-4 my-0" />
              </v-flex>

              <v-flex xs12 class="px-2">
                <h3>Resultados: </h3>
                <ul>
                  <li v-for="word in results" :key="`result-${word}`">
                    {{ word }}
                  </li>
                </ul>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      words: [
        [],
        [],
        [],
        [],
        []
      ],

      options: [
        'abajo',
        'altar',
        'bajar',
        'bomba',
        'bueno',
        'cable',
        'cerca',
        'corto',
        'delta',
        'donde',
        'estar',
        'falta',
        'gomas',
        'grasa',
        'hogar',
        'hueso',
        'listo',
        'lugar',
        'magia',
        'miedo',
        'mundo',
        'nunca',
        'otros',
        'pasar',
        'pasos',
        'perca',
        'plata',
        'punto',
        'queso',
        'ronda',
        'salto',
        'suena',
        'tasar',
        'traba',
        'valor'
      ]
    }
  },

  computed: {
    results () {
      let results = []

      let opts = this.words[0]

      if (opts.length > 0) {
        results = this.options.filter((item, index) => {
          return opts.includes(item[0])
        })
      }

      opts = this.words[1]

      if (opts.length > 0) {
        results = results.filter((item, index) => {
          return opts.includes(item[1])
        })
      }

      opts = this.words[2]

      if (opts.length > 0) {
        results = results.filter((item, index) => {
          return opts.includes(item[2])
        })
      }

      opts = this.words[3]

      if (opts.length > 0) {
        results = results.filter((item, index) => {
          return opts.includes(item[3])
        })
      }

      opts = this.words[4]

      if (opts.length > 0) {
        results = results.filter((item, index) => {
          return opts.includes(item[4])
        })
      }

      return results
    }
  }
}
</script>